// core
import { lazy, Suspense } from 'react';
// router
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom'
import ScrollToTop from 'component/utility/ScrollToTop'
// recoil
import { useRecoilValue } from 'recoil';
import { token } from 'store/index'
// component - utility
import ErrorBoundary from './component/utility/ErrorBoundary'  // 
import LoadingPage from './component/utility/LoadingPage'  // 
import MessageBundle from './component/utility/MessageBundle'  //  
// component - layout
import Header from './component/layout/Header'  // 
import Footer from './component/layout/Footer'  // 
// page
import Home from './page/Home/Home' // 
import Trend from './page/Trend/Trend'  // 
import Recommend from './page/Recommend/Recommend'  //  
import Search from './page/Search/Search' // 
import Community from './page/Community/Community'  //  
const Spot = lazy(() => import('./page/Spot/Spot')); //  
import Login from './page/Account/Login'  // 
const Signup = lazy(() => import('./page/Account/Signup')); // 
const ChooseTheme = lazy(() => import('./page/Account/ChooseTheme')); //  
const FindPassword = lazy(() => import('./page/Account/FindPassword')); //  
import Mypage from './page/Mypage/Mypage' //  
const AdApplication = lazy(() => import('./page/AdApplication/AdApplication'));  //   
const Customer = lazy(() => import('./page/Customer/Customer'));  // 
const PrivacyPolicy = lazy(() => import('./page/Customer/PrivacyPolicy'));  // 
const Terms = lazy(() => import('./page/Customer/Terms'));  // 
const EmailDenial = lazy(() => import('./page/Customer/EmailDenial'));  // 
import NotFound from './page/NotFound'  // 404 NotFound

function App() {
  const accessToken = useRecoilValue(token.accessToken);

  return (
    <div className="App">
      <MessageBundle />
      <Router>
        <Header />
        <ScrollToTop />
        <ErrorBoundary>
          <Suspense fallback={<LoadingPage />}>
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/trend" element={<Trend />} />
              <Route path="/recommend" element={
                accessToken ? <Recommend /> : <Navigate to="/login" replace={true} />
              } />
              <Route path="/search" element={<Search />} />
              <Route path="/community" element={<Community />} />
              <Route path="/spot/:spotId" element={<Spot />} />
              <Route path="/login" element={<Login />} />
              {/* <Route path="/login" element={
                !accessToken ? <Login /> : <Navigate to="/" replace={true} />
              } /> */}
              <Route path="/signup" element={<Signup />} />
              <Route path="/choosetheme" element={
                accessToken ? <ChooseTheme /> : <Navigate to="/login" replace={true} />
              } />
              <Route path="/findpass" element={<FindPassword />} />
              <Route path="/mypage" element={
                accessToken ? <Mypage /> : <Navigate to="/login" replace={true} />
              } />
              <Route path="/adapplication" element={<AdApplication />} />
              <Route path="/customer" element={<Customer />} />
              <Route path="/privacypolicy" element={<PrivacyPolicy />} />
              <Route path="/emaildenial" element={<EmailDenial />} />
              <Route path="/terms" element={<Terms />} />
              <Route path="*" element={<NotFound />} />
            </Routes>
          </Suspense>
        </ErrorBoundary>
        <Footer />
      </Router>
    </div>
  );
}

export default App;